import { gsap } from 'gsap';

import swiper from './swiper';
// import './threeBackground';

const bar = document.querySelector('.loading__bar--inner');
const counterNumber = document.querySelector('.loading__counter--number');

let currentWidth = 0;

const barInterval = setInterval(() => {
    counterNumber.textContent = `${currentWidth}%`;
    bar.style.width = `${currentWidth++}%`;
    if (currentWidth === 101) {
        clearInterval(barInterval);

        gsap.to('.loading__bar', {
            duration: 5,
            rotate: '180deg',
            left: '1000%',
        });

        gsap.to('.loading__text, .loading__counter', {
            duration: 0.5,
            opacity: 0,
        });

        gsap.to('.loading__box', {
            duration: 1,
            borderRadius: '50%',
            height: '50rem', // currentWidth
        });

        gsap.to('.loading__svg', {
            duration: 10,
            opacity: 1,
            rotate: '360deg',
        });

        gsap.to('.loading__box', {
            duration: 1,
            delay: 2,
            border: 'none',
        });

        gsap.to('.loading', {
            duration: 2,
            delay: 2,
            zIndex: 1,
            background: 'transparent',
            opacity: 0.5,
        });

        gsap.to('.loading__svg', {
            duration: 100,
            delay: 2,
            rotate: '360deg',
        });

        gsap.to('.loading', {
            duration: 0.5,
            delay: 2.5,
            position: 'absolute',
        });

        gsap.to('.header', {
            duration: 2,
            delay: 2,
            top: '0',
        });

        gsap.to('.scroll-down', {
            duration: 2,
            delay: 3,
            bottom: '4.8rem',
            opacity: 1,
        });

        gsap.to('.introduction', {
            duration: 1.5,
            delay: 4,
            left: '2rem',
        });
    }
}, 20);
