import { Swiper, Pagination } from 'swiper';

Swiper.use([Pagination]);

const swiper = new Swiper('.swiper', {
    slidesPerView: 3,
    spaceBetween: 30,
    pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
        clickable: true,
    },
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
    breakpoints: {
        0: {
            slidesPerView: 1,
            spaceBetween: 100,
        },
        // when window width is <= 320px
        320: {
            slidesPerView: 1,
            spaceBetween: 100,
        }, // when window width is <= 730px
        730: {
            slidesPerView: 2,
            spaceBetween: 50,
        },

        // when window width is <= 1100px
        1100: {
            slidesPerView: 3,
            spaceBetween: 50,
        },

        // when window width is <= 1550px
        1550: {
            slidesPerView: 4,
            spaceBetween: 70,
        },
    },
});

export default swiper;
